@import url(https://fonts.googleapis.com/css?family=Merriweather|Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Merriweather', serif; }

html {
  font-size: 16px; }

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 100%; }

input {
  font-size: 1em; }

.App {
  max-width: 794px;
  width: 100%;
  margin: 0 auto;
  padding: 2em;
  box-sizing: border-box;
  padding-top: 1em;
  overflow: hidden; }

img {
  max-width: 100%; }

.designer-name {
  text-transform: capitalize; }

.location, .learn-more {
  color: #908a8a; }

.studio {
  background-color: #eee8e8; }
  .studio .tab-navigation-bar {
    margin-bottom: 1em; }
  .studio .intro, .studio .contact {
    height: 86vh; }

.search {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 1em; }
  .search input {
    padding: .5em;
    width: 93%;
    border-radius: 5px;
    text-transform: capitalize; }
  .search .input-glass {
    grid-column: 2;
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center; }
    .search .input-glass .fa {
      right: 8px;
      position: absolute;
      font-size: 60%; }

.search-result {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  min-height: 70px;
  text-transform: capitalize; }
  .search-result .thumbnail-container {
    width: 25%;
    margin-right: 10px; }

.tab-navigation-bar {
  margin-bottom: 1em; }

.tab-navigation {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }
  .tab-navigation .button {
    border-radius: 0;
    display: inline-block;
    border: none;
    padding: 1rem 1rem;
    margin: 0;
    text-decoration: none;
    background: #ffffff;
    color: #333333;
    font-family: sans-serif;
    font-size: 1rem;
    cursor: pointer;
    text-align: center;
    -webkit-transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
    transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
    transition: background 250ms ease-in-out, transform 150ms ease;
    transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 1px solid #ccc; }
    .tab-navigation .button:focus, .tab-navigation .button:hover {
      background: rgba(185, 9, 11, 0.8);
      color: #ffffff;
      outline: 0; }
    .tab-navigation .button.active {
      background: #b9090b;
      color: #ffffff; }

.card {
  text-align: center;
  width: 70vw;
  margin: 0 auto;
  min-height: 50vh;
  max-width: 625px; }

.description-container,
.contact .card {
  background: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1em auto;
  padding: 0 1em; }

.autocomplete-wrapper {
  width: 100%; }

@media only screen and (max-width: 480px) {
  .App {
    padding: 0; }
  .search {
    grid-template-columns: 1fr; }
    .search .input-glass {
      width: 90%;
      grid-column: 1;
      margin: 0 auto;
      margin-top: 1em; }
      .search .input-glass .fa {
        right: 28px; }
  .card {
    width: 100vw; } }

